@import "../field/field";

@include field("radiosfield");

.radiosfield-input {
	@include field-input("radiosfield");

	&.mod-row {
		@extend %radiosfield-row;
	}
	&.button-group {
		display: flex;
	}
}

.form.mod-compact .radiosfield, .radiosfield.mod-compact {
	@include field-compact("radiosfield");
	align-items: flex-start;
	.radiosfield-input {
		@extend %radiosfield-row;
	}
}

.form.mod-framed .radiosfield, .radiosfield.mod-framed {
	@include field-framed("radiosfield");

	.radiosfield-input {
		@extend %radiosfield-row;
	}
}

%radiosfield-row {
	display: flex;
	flex-wrap: wrap;
	.radio {
		margin-top: 0;
		margin-bottom: 0;
	}
	.radio {
		margin-right: _component("field.horizontal-spacing");
	}
}
