$field: (
	font-size: _theme("sizes.standard.font-size"),
	horizontal-spacing: 1.5rem,

	input: (
		color: _color("text.default"),
		inline-margin: .15rem,
	),

	label: (
		color: _color("text.default"),
		font-size: _theme("sizes.small.font-size"),
		margin-bottom: .25rem,
	),

	message: (
		font-size: _theme("sizes.small.font-size"),
		margin-top: .35rem,
	),

	sizes: (
		shortest: 3rem,
		shorter: 6rem,
		short: 9rem,
		default: 13rem,
		long: 17rem,
		longer: 22rem,
		longest: 28rem
	),

	compact: (
		padding: .5rem,
		label: (
			color: _color("text.light"),
			font-size: _theme("sizes.standard.font-size"),
			right-margin: .3rem,
			sizes: (
				shorter: 3rem,
				short: 5rem,
				default: 7rem,
				long: 9rem,
				longer: 11rem
			)
		)
	),

	framed: (
		default-palette:(
			color: _color("text.light", "color", true),
			see-through: transparentize(_color("text.light", "color", true), .95),
			text: _color("text.default", "color", true),
			placeholder: #CCCCCC
		),
		side-padding: 1rem,
		top-padding: 2.2rem,
		bottom-padding: .7rem,
		label-top-offset: .5rem,
		textarea: (
			height: 7rem,
			margin-top: 1.5rem
		),
		title: (
			font-size: _theme("sizes.big.font-size"),
			line-height: _theme("sizes.big.line-height")
		)
	)
);

$theme: _set($theme, "components.field", $field);
