$button: (

	// theming
	default-palette: (
		color: _theme("commons.background.base"),
		light: _theme("commons.background.dark"),
		dark: _theme("commons.background.darker"),
		text: _color("text.default")
	),
	margin: .2rem,
	line-height: 1.15,
	font-weight: 600,
	box-shadow: (
		offset: -2px,
		color: rgba(0, 0, 0, .1)
	),

	// mod
	wired: (
		default-palette: _getMap("palettes.primary"),
	),
	flat: (
		color: black,
	),

	// sizing
	sizing: (
		smaller: (
			padding: .25rem .5rem,
			font-size: _theme("sizes.smaller.font-size")
		),
		small: (
			padding: .33rem .66rem,
			font-size: _theme("sizes.small.font-size")
		),
		default: (
			padding: .5rem 1rem,
			font-size: _theme("sizes.standard.font-size")
		),
		big: (
			padding: .75rem 1.5rem,
			font-size: 1.15rem
		),
	),
);
$theme: _set($theme, "components.button", $button);
